.feedback input {
  background-color: @input-color;
  border: none;
}

.feedback__section-title {
  .common-text();
  .container-mob();
  display: block;
  margin: 0 auto;
  padding: 23px 0;
  line-height: 25px;
  text-align: center;

  &--smaller {
    padding-top: 18px;
  }

  &--bigger {
    padding-bottom: 18px;
  }

  &--last {
    padding-bottom: 15px;
  }

  &:last-child {
    padding-bottom: 16px;
  }

  @media (min-width: @tablet-width) {
    .container-tablet();
    font-size: 21px;
    line-height: 26px;
    padding: 0;
    text-align: left;

    &--smaller {
      padding-bottom: 56px;
      margin: 0;
    }

    &--custom {
      text-align: center;
      padding-top: 24px;
      padding-bottom: 26px;
      width: 400px;
    }

    &--lefter {
      margin: 0;
    }
  }

  @media (min-width: @desktop-width) {
    .container-desktop();

    &--smaller {
      margin: 0;
      padding-bottom: 57px;
    }

    &--lefter {
      text-align: start;
      padding-left: 75px;
      margin: 0;
    }

    &--custom {
      padding-top: 27px;
      padding-bottom: 24px;
      width: 400px;
    }

    &--last {
      padding-top: 34px;
    }
  }
}

.feedback__section {
  position: relative;
  border: none;
  padding: 0;
  width: 100%;

  &--divided {
    &::after {
      content: "";
      position: absolute;
      bottom: -5px;
      left: -20px;
      display: block;
      width: 320px;
      height: 1px;
      background-color: @grey-crossline;
    }

    @media (min-width: @tablet-width) {
      &::after {
        content: none;
      }
    }
  }

  @media (min-width: @tablet-width) {
    &:nth-child(1) {
      width: 266px;
      margin: 0;
      padding: 0;
      order: 2;
    }

    &:nth-child(3) {
      width: 324px;
      margin: 0;
      padding: 0;
      order: 1;
    }

    &:nth-child(2) {
      order: 3;
      border-top: 1px solid @input-iconcolor;
      margin-top: 23px;
      padding-bottom: 3px;
    }

    &:nth-child(4) {
      order: 4;
      border-top: 1px solid @input-iconcolor;
    }

    &:nth-child(5) {
      order: 5;
      border-top: 1px solid @input-iconcolor;
    }

    &:nth-child(6) {
      order: 6;
    }
  }

  @media (min-width: @desktop-width) {
    &:nth-child(3) {
      width: 484px;
      padding-left: 8px;
    }

    &:nth-child(1) {
      width: 403px;
      padding-right: 3px;
    }

    &:nth-child(2) {
      margin-top: 21px;
    }

    &:nth-child(5) {
      margin-top: -4px;
    }
  }
}

.feedback {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .container-mob();
  max-width: 285px;

  @media (min-width: @tablet-width) {
    .container-tablet();
    padding-top: 55px;
    max-width: 649px;
  }

  @media (min-width: @desktop-width) {
    .container-desktop();
    padding-top: 0;
    max-width: 966px;
  }
}

.feedback__section-contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  padding-bottom: 10px;
  flex-wrap: wrap;

  @media (min-width: @tablet-width) {
    padding-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    .container-desktop();
    flex-direction: row;
    justify-content: normal;
  }
}

.feedback__section-title--visited-place {
  max-width: 280px;

  @media (min-width: @tablet-width) {
    padding-top: 27px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 23px;
  }
}
